import Event from 'channel/events';
import { is } from './lang';

const trimRightUndefine = (args = []) => {
	const result = [...args];
	for (let i = result.length - 1; i >= 0; i--) {
		if (result[i] === undefined) {
			result.pop();
		} else {
			break;
		}
	}

	return result;
};

const DELEGATE_EVENT_NS = '.delegate-events-';
const EVENT_KEY_SPLITTER = /^(\S+)\s*(.*)$/;
const EXPRESSION_FLAG = /{{([^}]+)}}/g;
const INVALID_SELECTOR = 'INVALID_SELECTOR';

// 解析 eventKey 中的 {{xx}}, {{yy}}
const parseExpressionInEventKey = (selector, widget) => selector.replace(EXPRESSION_FLAG, (m, name) => {
	const parts = name.split('.');
	let point = widget;
	let part;

	while ((part = parts.shift())) {
		point = point[part];
	}

	// 已经是 className，比如来自 dataset 的
	if (is.string(point)) {
		return point;
	}

	return INVALID_SELECTOR;
});

function parseEventKey(eventKey, widget) {
	const match = eventKey.match(EVENT_KEY_SPLITTER);
	const eventType = match[1] + DELEGATE_EVENT_NS + widget.id;

	// 当没有 selector 时，需要设置为 undefined，以使得 zepto 能正确转换为 bind
	let selector = match[2] || undefined;

	if (selector && selector.indexOf('{{') > -1) {
		selector = parseExpressionInEventKey(selector, widget);
	}

	return {
		type: eventType,
		selector
	};
}

class Widget {
	constructor(element, options = {}) {
		// 初始化 element
		this.initElement(element);

		// 初始化 attrs
		this.attrs = this.initAttrs(options);

		// 初始化 events
		this.events = this.initEvents() || {};

		// 绑定事件
		this.delegateEvents();

		// 子类自定义的初始化
		this.setup();
	}

	initElement(element) {
		this.el = $(element);

		// 如果对应的 DOM 元素不存在，则报错
		if (!this.el || !this.el[0]) {
			throw new Error('el is invalid');
		}

		this.element = this.el[0];
	}

	delegateEvents(el, events, handler) {
		/* eslint-disable */
		const args = trimRightUndefine(Array.prototype.slice.call(arguments));
		/* eslint-enable */

		// widget.delegateEvents()
		if (args.length === 0) {
			events = this.events;
			el = this.el;

		// widget.delegateEvents({
		//   'click p': 'fn1',
		//   'click li': 'fn2'
		// })
		} else if (args.length === 1) {
			events = el;
			el = this.el;

		// widget.delegateEvents('click p', function(ev) { ... })
		} else if (args.length === 2) {
			handler = events;
			events = el;
			el = this.el;

		// widget.delegateEvents(el, 'click p', function(ev) { ... })
		} else {
			/* eslint-disable */
			el || (el = this.el);
			this.__delegateEls || (this.__delegateEls = []);
			this.__delegateEls.push($(el));
			/* eslint-enable */
		}

		// 'click p' => {'click p': handler}
		if (is.string(events) && is.func(handler)) {
			const o = {};
			o[events] = handler;
			events = o;
		}

		// key 为 'event selector'
		for (const key of Object.keys(events)) {
			const result = parseEventKey(key, this);
			const eventType = result.type;
			const selector = result.selector;

			const callback = (ev) => {
				const cb = events[key];

				if (is.func(cb)) {
					return cb.call(this, ev);
				} else {
					return this[cb](ev);
				}
			};

			// delegate
			if (selector) {
				$(el).on(eventType, selector, callback);
			// normal bind
			// 分开写是为了兼容 zepto，zepto 的判断不如 jquery 强劲有力
			} else {
				$(el).on(eventType, callback);
			}
		}
	}

	initAttrs(attrs = {}) {
		return attrs;
	}

	initEvents(events = {}) {
		return events;
	}

	get(attrName) {
		if (!attrName) { return; }
		if (this.attrs && (this.attrs[attrName] !== undefined)) {
			return this.attrs[attrName];
		}
	}

	set(attrName, value) {
		if (this.attrs && (this.attrs[attrName] !== undefined)) {
			this.attrs[attrName] = value;
		}
	}

	// 子类自定义的初始化
	setup() {}

	// 子类自定义的渲染
	render() {}

	// 在 this.el 内寻找匹配节点
	$(selector) {
		return this.el.find(selector);
	}
}

Event.mixTo(Widget);

export default Widget;



