require('zepto/zepto');
require('zepto/ie');
require('zepto/event');

/* require('zepto/callbacks');
require('zepto/deferred');*/

require('zepto/ajax');

// outerHeight outerWidth
$.each(['Height', 'Width'], (index, prop) => {
	const fn = `outer${prop}`;

	if ($.fn[fn]) return;

	$.fn[fn] = function () {
		return this.length ? this[0][`offset${prop}`] : undefined;
	};
});



