import Event from 'channel/events';
import Cookie from 'js-cookie/cookie';
import Device from './core/utils/device';
import Plugin from './core/utils/plugin';
import Widget from './core/utils/widget';

require('./core/zepto');

/*
import Request from 'channel/requests';
Event.mixTo(Request);
const radio = new Request();*/

const radio = new Event();

const plugin = new Plugin({
	namespace: 'fnp'
});

window.FN = {
	_Event: Event,
	Widget,
	Cookie,
	Device,
	Radio: radio,
	Plugin: plugin
};

radio.on('app:init', (...args) => {
	plugin.render(...args);
});



