import uuid from '../string/uuid';

export default class Plugin {
	constructor({ namespace = 'fnp' }) {
		this.namespace = namespace;
		this._plugins = {};
	}

	register(widgets = {}) {
		this._widgets = {
			...this._widgets,
			...widgets
		};
	}

	query(key) {
		const ns = this.namespace;
		if (typeof key !== 'string') {
			key = $(key).attr(`${ns}-rendered`);
		}

		return this._plugins[key];
	}

	render(scope = 'body') {
		const $scope = $(scope);
		const ns = this.namespace;

		let elements = [$scope.find(`[${ns}]`)];

		if ($scope.attr(ns)) {
			elements = [$scope, ...args];
		}

		for (const element of elements) {
			$(element).each((_, e) => {
				this.renderElement(e);
			});
		}
	}

	renderElement(el) {
		el = $(el);
		const ns = this.namespace;
		const widget = this._widgets || {};
		const instances = this._plugins;

		if (el.attr(`${ns}-rendered`)) {
			return;
		}

		let options = $.trim(el.attr(`${ns}-options`));
		if (options) {
			if (options.substring(0, 1) !== '{') {
				options = `{${options}}`;
			}

			/*eslint-disable */
			options = (new Function(`return ${options}`))();
			/*eslint-enable */
		} else {
			options = {};
		}

		const Clz = widget[el.attr(ns)];

		if (Clz) {
			const id = uuid();
			const ins = new Clz(el, {
				id,
				...options
			});
			ins.render();
			instances[id] = ins;

			el.attr(`${ns}-rendered`, id);
		}
	}
}



